.content__item-img img {
    border-radius: 100px;
    width: 64px;
    height: 64px;
  }
  
  .content__item-value {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  
  .content__item-value h4, p {
    margin: 0;
  }
  
  .section {
    display: flex;
    flex: 1 1 auto;
    padding: 25px;
    /* margin: auto; */
    border: 1px solid rgb(243, 243, 243);
    background-color: rgb(243, 243, 243);
    border-radius: 3px;
    box-shadow: 0 2px 23px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  }
  
  .container {
    /* width: 100%; */
    /* margin: 0 auto; */
  }
  
  .content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .image img {
    max-width: 128px;
    max-height: 128px;
  }
  
  .content__inner {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: wrap;
    /* width: 100%; */
    padding: 5px;
    /* border: 1px solid red; */
  }
  .content__title {
    /* /* не работает с флексом...(( */

    white-space: wrap;
    overflow-x: hidden;
    text-overflow: ellipsis; 
  }

  .content__description {

    /* flex: 400px 400px auto; */
    /* flex-wrap: wrap; */
    /* width: 100%; */
    /* white-space: wrap; */
    /* border: 1px solid blue; */
  }
  
  .content h1{
    margin: 0;
  }
  
  .btn button {
    padding: 0;
    border: none;
    background-color: transparent;
    
    cursor: pointer;
  }
  
  .btn button > .icon {
    width: 62px;
    height: 62px;
  }

  .btn button:hover {
    /* width: 62px;
    height: 62px; */
    fill: red;
    stroke: red;
    /* background-color: aquamarine; */
    color: red;
    filter:drop-shadow(2px, 2px, 22px, 22px)
  }
  .red {
    fill: red;
    stroke: red;
    /* background-color: aquamarine; */
    color: red;
  }